import React from 'react';
import './button.css';

function Button(props) {

    const { variant = 'aqua', children, headline, text, ...rest} = props

    return (
        <button className={`button ${variant}`} {...rest}>
            {children}
            <h1>{headline}</h1>
            <p>{text}</p>
        </button>
    )
}

export default Button;
